@font-face {
    font-family: 'Dubai';

    src: url('../fonts/DubaiW23-Regular.eot'); /* IE9 Compat Modes */
    src: url('../fonts/DubaiW23-Regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
    url('../fonts/DubaiW23-Regular.woff2') format('woff2'), /* Super Modern Browsers */
    url('../fonts/DubaiW23-Regular.woff') format('woff'), /* Pretty Modern Browsers */
    url('../fonts/DubaiW23-Regular.ttf')  format('truetype'); /* Safari, Android, iOS */
}

